"use client";
import { useEffect, useState } from "react";
import Link from "next/link";
import { supabase } from "@/lib/supabaseClient";

export default function TareasTecnico() {
  const [tareas, setTareas] = useState([]);

  async function cargar() {
    const tecnico = (await supabase.auth.getUser()).data.user.id;

    const { data } = await supabase
      .from("ordenes_trabajo")
      .select("*, maquinas(*), clientes(nombre)")
      .eq("tecnico_id", tecnico)
      .order("id", { ascending: false });

    setTareas(data || []);
  }

  useEffect(() => {
    cargar();
  }, []);

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Mis Tareas</h1>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {tareas.map((t) => (
          <Link
            key={t.id}
            href={`/tecnico/tarea/${t.id}`}
            className="border p-4 rounded shadow hover:bg-gray-100"
          >
            <img
              src={t.maquinas?.foto}
              className="h-40 w-full object-cover rounded"
            />

            <h2 className="font-bold mt-2">{t.maquinas?.codigo}</h2>
            <p className="text-sm text-gray-600">
              {t.descripcion_problema}
            </p>

            <span
              className={`inline-block mt-2 px-3 py-1 rounded text-white ${
                t.estado === "pendiente"
                  ? "bg-yellow-600"
                  : t.estado === "en_proceso"
                  ? "bg-blue-600"
                  : "bg-green-600"
              }`}
            >
              {t.estado.toUpperCase()}
            </span>
          </Link>
        ))}
      </div>
    </div>
  );
}
